.icon {
    @include Vertical-inline;
    @include Hide_txt;
    &.filter {
        @include Background($sprite:'ico-select', $type:'png', $size:100% auto, $position:center center);
        width: 1.1rem;
        height: 1.1rem; 
    }
    &.filterClose {
        @include Background($sprite:'ico-close', $type:'png', $size:100% auto, $position:center center);
        width: 0.7rem;
        height: 0.7rem;
    }
    &.fast {
        @include Background($sprite:'ico-estimate-btn1', $type:'png', $size:100% auto, $position:center center);
        width: 0.9rem;
        height: 0.9rem;
    }
    &.pro {
        @include Background($sprite:'ico-estimate-btn2', $type:'png', $size:100% auto, $position:center center);
        width: 0.9rem;
        height: 0.9rem;
    }
    &.prev {
        @include Background($sprite:'ico-prev', $type:'png', $size:100% auto, $position:center center);
        width: 0.5rem;
        height: 1rem;
    }
    &.draw {
        @include Background($sprite:'ico-draw', $type:'png', $size:100% auto, $position:center center);
        width: 1.7rem;
        height: 1.7rem;
    }
    &.info {
        @include Background($sprite:'ico-info', $type:'png', $size:100% auto, $position:center center);
        width: 1.3rem;
        height: 1.3rem;
    }
    &.info2 {
        @include Background($sprite:'ico-info2', $type:'png', $size:100% auto, $position:center center);
        width: 1.3rem;
        height: 1.3rem;
    }
    &.fuelG {
        @include Background($sprite:'ico-fuel-g', $type:'png', $size:100% auto, $position:center center);
        width: 2.2rem;
        height: 2.2rem;
    }
    &.fuelD {
        @include Background($sprite:'ico-fuel-d', $type:'png', $size:100% auto, $position:center center);
        width: 2.2rem;
        height: 2.2rem;
    }
    &.fuelL {
        @include Background($sprite:'ico-fuel-l', $type:'png', $size:100% auto, $position:center center);
        width: 2.2rem;
        height: 2.2rem;
    }
    &.fuelH {
        @include Background($sprite:'ico-fuel-h', $type:'png', $size:100% auto, $position:center center);
        width: 2.2rem;
        height: 2.2rem;
    }
    &.fuelE {
        @include Background($sprite:'ico-fuel-e', $type:'png', $size:100% auto, $position:center center);
        width: 2.2rem;
        height: 2.2rem;
    }

}


.flag {
    height: 1.7rem;
    border-radius: 1rem;
    padding: 0 .7rem;
    font-size: .9rem;
    box-sizing: border-box;
    @include Inline-align-middle;
    @include Nomal;
    &.best {
        border: 1px solid #25C1D6;
        color: #25C1D6;
        margin-left: .4rem;
    }
    &.low {
        border: 1px solid #9198FF;
        color: #9198FF;
        margin-left: .4rem;
    }
}
